@mixin webkit-scrollbar-style-1 {
    ::-webkit-scrollbar {
        width: 10px;
        height: 10px;
    }
    ::-webkit-scrollbar-thumb {
        background-color: rgb(164, 118, 170);
        border-radius: 5px;
    }
    ::-webkit-scrollbar-thumb:hover {
        background-color: plum;
    }
}